body{
    padding:0;
    margin:0;
    overflow-x: hidden;
}

.logo{
    width:35px;
    height:35px;
    border: 2px solid rgba(255,0,0,0.9);
    background-color:  rgba(255,0,0,0.9);
    border-radius: 50%;
    box-shadow: 0 0 5px #ff0000,
                0 0 25px #ff0000,
                0 0 50px #ff0000,
                0 0 200px #ff0000;
    
    padding:0;
}
.TitleLogo{
    margin-left: -5px;
    margin-top: -35px;
    text-align:center;
    padding: 4px;
    border-radius: 50px;
    font-size: 15px;
    color: gainsboro;
    font-weight: bold;
}
.TitleLogo:hover{
    color: black;
    background-color: rgba(255,0,0,0.7);
    border:1px solid rgba(255,0,0,0.7);
    box-shadow: 0 0 5px #ff0000,
                0 0 25px #ff0000,
                0 0 50px #ff0000,
                0 0 200px #ff0000;

}
.navbar .nav-item{
    text-align:center;
    margin:auto;
    padding-left:30px;
    padding-right:30px;
}
.navbar .nav-item:hover{
    text-align: center;
    background-color: rgba(255,0,0,0.4);
    border: none;
    box-shadow: 0 0 5px #ff0000,
                0 0 25px #ff0000,
                0 0 50px #ff0000,
                0 0 200px #ff0000;
}
.navbar .nav-item .dropdown-menu {
    background-color: rgba(0,0,0,0.8);
    color: gainsboro;
    padding:5px;
}
.dropdown-item{
    border: 2px solid rgba(255,0,0,0.6);
    margin:2px;
    color: gainsboro;
    padding:3px;
    text-align: center;
}
.navbar .nav-item .dropdown-menu .dropdown-item:hover {
    background-color: rgba(255,0,0,0.4);
    color: gainsboro;
}
.text-span{
    font-size: 10px;
}
.slider{
    
}

.sliderDiv img{
    width: 100vw;
    height: 100vh;
    position:relative;
    margin: 0;
    padding:0;
    opacity: .55;
     
}
.text-home{
    position:sticky;
    left:33%;
    z-index: 99;
    text-align:center;
    font-weight: bolder;
    height: 0px;
    
    top: 40%;
}
.Questions{
    position:absolute;
    top:60%;
    left:18%;
    margin:auto;
    text-align:center;
    } 
.content{ 
    margin:auto;
    text-align:center;
}
.content h3{
    margin-top:15px;
}
.icon-hand{
    background-color: rgba(0,0,0,0.7);
    border-radius:50%;
    width:150px;
    height:150px;
    display:flex;
    align-items:center;
    justify-content:center;
    margin-bottom:15px;
    margin:auto;
    cursor:pointer;
}
.icon-question{
    background-color: rgba(0,0,0,0.7);
    border-radius:50%;
    width:150px;
    height:150px;
    display:flex;
    align-items:center;
    justify-content:center;
    margin-bottom:15px;
    margin:auto;
    cursor:pointer;
}
.icon-question:hover, .icon-hand:hover{
    width:160px;
    height:160px;
    box-shadow: 0 0 5px #ff0000,
                0 0 25px #ff0000,
                0 0 50px #ff0000,
                0 0 200px #ff0000;
}
.content hr{
    border-top: 4px solid black;
    color:black;
    margin:auto;
    text-align:center;
}
.icon-question i:hover, .icon-hand i:hover{
    font-size:90px;
}
.icon-question i ,.icon-hand i {
    font-size:80px;
    color: rgba(255,140,0,0.9);
}
.steel{
    display:flex;
    justify-content:center;
    position:absolute;
    z-index:1;
    top: 20%;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(240,255,255,0.5);
}
.steel-hr{
    width:450px;
    border-top:8px solid black;
    position:absolute;
    left:0;
    right:0;
    top: 43%;
    margin:auto;
    z-index:10;
}
.steel h3{
    font-size:40px;
    margin-top:100px;
}
.steel-icon i{
    font-size:40px;
    margin-left:20px;
    margin-top:100px;
}
.content-steel p{
    font-size:16px;
    width:50%;
    text-align:center;
    position:absolute;
    font-weight: 900;
    color:black;
    top:50%;
    left:0;
    right:0;
    margin:auto;
    z-index:100;
}
/* ============ desktop view ============ */
@media all and (min-width: 992px) {
	.navbar .nav-item .dropdown-menu{ display: none; }
	.navbar .nav-item:hover .nav-link{   }
	.navbar .nav-item:hover .dropdown-menu{ display: block; }
	.navbar .nav-item .dropdown-menu{ margin-top:0; }
}	
/* ============ desktop view .end// ============ */

@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
  .sliderDiv img{
      height: 100%;
  }
  .text-home{
      left:15%;
      top: 30%;
  }
  .text-home h1 {
      font-size: 16px;
  }
  .text-home h3{
      font-size: 12px;
  }
}

